<template>
  <div
    class="sidebar"
    :data-color="activeColor"  
  >
    <div class="logo">
      <sui-grid :columns="2">
        <sui-grid-row>
          <sui-grid-column>
            <a href="#" class="simple-text logo-mini">
            <div class="logo-img">
              <img :src="imgLogo" />
            </div>
          </a>
          </sui-grid-column>
          <sui-grid-column>
            <a
              href="#"
              target="_blank"
              class="simple-text logo-normal"
              style="margin-left: -25px"
            >
            {{ title }}
            </a>
          </sui-grid-column>
        </sui-grid-row>
      </sui-grid> 
    </div>
    <div class="sidebar-wrapper">
      <slot name="content"></slot>
      <md-list class="nav">
        <!--By default vue-router adds an active class to each route link. This way the links are colored when clicked-->
        <slot>
          <sidebar-link
            v-for="(link, index) in sidebarLinks"
            :key="link.name + index"
            :to="link.path"
            :link="link"
          >
          </sidebar-link>
        </slot>
      </md-list>
    </div>
  </div>
</template>
<script>
import SidebarLink from "./SidebarLink.vue";

export default {
  components: {
    SidebarLink
  },
  props: {
    title: {
      type: String,
      default: "OASIS NAILS & BEAUTY"
    },
    imgLogo: {
      type: String,
      default: require("@/assets/img/logo.png")
    },
    activeColor: {
      type: String,
      default: "pink",
      validator: value => {
        let acceptedValues = ["","green", "purple", "blue", "pink", "orange", "red"];
        return acceptedValues.indexOf(value) !== -1;
      }
    },
    sidebarLinks: {
      type: Array,
      default: () => []
    },
    autoClose: {
      type: Boolean,
      default: true
    }
  },
  provide() {
    return {
      autoClose: this.autoClose
    };
  },
  
};
</script>
<style>
@media screen and (min-width: 991px) {
  .nav-mobile-menu {
    display: none;
  }
}
</style>
